// 
import ColorPicker from './color-picker'
import Button from './button'
// 
const components = [
  ColorPicker,
  Button
]
//  install  Vue  use 
const install = function (Vue) {
  // 
  components.forEach(component => {
    Vue.component(component.name, component)
  })
}
// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}
export default {
  //  install Vue.use() 
  install,
  // 
  ColorPicker,
  Button
}
